import Vue from 'vue'
import VueRouter from 'vue-router'
import routes from './components/Single/routers.js'
import App from './components/Single/App.vue'

Vue.use(VueRouter)
// import oilstation from '../components/oilstation.vue'

Vue.filter('formatDate', function (value) {
    return (new Date(value * 1000)).Format('yyyy-MM-dd hh:mm:ss')
})

const router = new VueRouter({
    routes: routes
})

Vue.directive('select', {
    inserted (el, binding, vnode) {
        let optionsStr = ''
        if (Object.prototype.toString.call(binding.value) === '[object Array]') {
            binding.value.forEach(function (elem) {
                optionsStr += '<option value="' + elem.value + '">' + elem.key + '</option>'
            })
        }
        $(el).html(optionsStr).selectpicker('refresh')
    },
    update (el, binding, vnode, oldVnode) {
        if (JSON.stringify(binding.value) !== JSON.stringify(binding.oldValue)) {
            let optionsStr = ''
            if (Object.prototype.toString.call(binding.value) === '[object Array]') {
                binding.value.forEach(function (elem) {
                    optionsStr += '<option value="' + elem.value + '">' + elem.key + '</option>'
                })
            }
            $(el).html(optionsStr).selectpicker('refresh')
        } else {
            $(el).selectpicker('refresh')
        }
    }
})

